import React from "react";
import {Element} from "react-scroll"

import "./skillcontainer.css";
import {Slider} from "antd"




const IconSlider = (props) => {
  
  return (
    <Element className="skillcontainer" id="skills">
      <div className="skillcontainer_image">
        <img src="images/skill.jpg" alt=""/>
      </div>
      <div className="skillcontainer_text1">
        <h2>SKILLSET</h2>
        <div className="icon-wrapper">  
      <div> 
         <h5 className="skillcontainer_text">
            HTML ( Hyper  Text    Markup  Language )
         </h5>
          <div className="skillcontainer_slider1">
              <Slider {...props}  value={17} />
          </div>  
      </div>
      
      <div>
        <h5 className="skillcontainer_text" >
          CSS ( Cascadding  Style  Sheet )
        </h5>
        <div className="skillcontainer_slider2">
        <Slider {...props}  value={16} />
        </div>  
      </div>

      <div>
        <h5 className="skillcontainer_text">
          BOOTSTRAP
        </h5>
        <div className="skillcontainer_slider3">
        <Slider {...props}  value={14} />
        </div> 
      </div>

      <div>
        <h5 className="skillcontainer_text">
          REACT 
        </h5>
        <div className="skillcontainer_slider4">
        <Slider {...props}  value={12} />
        </div>
      </div>
      <div>
        <h5 className="skillcontainer_text">
          PHOTOSHOP 
        </h5>
        <div className="skillcontainer_slider5">
        <Slider {...props}  value={17} />
        </div>
      </div>
      
      </div>
      </div>
    </Element>
  );
};
const Skillcontainer = () => <IconSlider min={0} max={20} />;
export default Skillcontainer;

 
